/**
 * OpenTelemetry Integration for Lit Status SDK
 * 
 * Provides distributed tracing, metrics, and logging capabilities
 * for the executeAndLog method in the Lit Status monitoring SDK.
 * 
 * OpenTelemetry dependencies are optional - if not installed,
 * the SDK will work normally without telemetry features.
 */

// Configuration interface
export interface OpenTelemetryConfig {
  enabled: boolean;
  serviceName?: string;
  serviceVersion?: string;
  otlpEndpoint?: string;
  exportToConsole?: boolean;
  exportIntervalMs?: number;
}

// Default configuration
const DEFAULT_CONFIG: Required<OpenTelemetryConfig> = {
  enabled: true,
  serviceName: 'lit-status-sdk',
  serviceVersion: '1.0.0',
  otlpEndpoint: '',
  exportToConsole: true,
  exportIntervalMs: 10000,
};

export class LitStatusTelemetry {
  private config: Required<OpenTelemetryConfig>;
  private isInitialized = false;
  private hasOpenTelemetry = false;

  // OpenTelemetry instances (will be null if not available)
  private tracer: any = null;
  private meter: any = null;
  private logger: any = null;
  private functionExecutionCounter: any = null;
  private functionExecutionDuration: any = null;
  private functionErrorCounter: any = null;

  constructor(config: Partial<OpenTelemetryConfig> = {}) {
    this.config = { ...DEFAULT_CONFIG, ...config };
  }

  /**
   * Initialize OpenTelemetry providers and instruments
   */
  async initialize(): Promise<void> {
    if (!this.config.enabled || this.isInitialized) {
      return;
    }

    // Check if we're in a browser environment
    if (typeof globalThis !== 'undefined' && 'window' in globalThis) {
      console.warn(' OpenTelemetry is not supported in browser environments');
      return;
    }

    try {
      // Try to dynamically import and initialize OpenTelemetry
      await this.initializeOpenTelemetry();
      this.hasOpenTelemetry = true;
      this.isInitialized = true;
      console.log(' Lit Status SDK OpenTelemetry initialized');
    } catch (error: any) {
      console.warn(' OpenTelemetry not available (continuing without telemetry):', error.message);
      console.warn(' To enable OpenTelemetry, install the optional dependencies:');
      console.warn('   npm install @opentelemetry/api @opentelemetry/api-logs @opentelemetry/resources @opentelemetry/semantic-conventions @opentelemetry/sdk-metrics @opentelemetry/exporter-metrics-otlp-http @opentelemetry/sdk-logs @opentelemetry/exporter-logs-otlp-http @opentelemetry/sdk-trace-node @opentelemetry/exporter-trace-otlp-http @opentelemetry/sdk-trace-base');
    }
  }

  /**
   * Initialize OpenTelemetry with dynamic imports
   */
  private async initializeOpenTelemetry(): Promise<void> {
    try {
      // Test if basic OpenTelemetry APIs are available
      const { metrics, trace, SpanStatusCode } = await import('@opentelemetry/api');
      const { logs } = await import('@opentelemetry/api-logs');

      // If we get here, the APIs are available
      this.tracer = trace.getTracer(this.config.serviceName, this.config.serviceVersion);
      this.meter = metrics.getMeter(this.config.serviceName, this.config.serviceVersion);
      this.logger = logs.getLogger(this.config.serviceName, this.config.serviceVersion);

      // Create metric instruments
      if (this.meter) {
        this.functionExecutionCounter = this.meter.createCounter('lit_function_executions_total', {
          description: 'Total number of function executions',
          unit: '1',
        });

        this.functionExecutionDuration = this.meter.createHistogram('lit_function_execution_duration_ms', {
          description: 'Function execution duration in milliseconds',
          unit: 'ms',
        });

        this.functionErrorCounter = this.meter.createCounter('lit_function_errors_total', {
          description: 'Total number of function execution errors',
          unit: '1',
        });
      }

      // Note: For full OpenTelemetry functionality (exporters, etc.), 
      // users should configure their own OpenTelemetry setup in their application.
      // This basic integration provides tracing and metrics via the global providers.

    } catch (error: any) {
      throw new Error(`OpenTelemetry packages not available: ${error.message}`);
    }
  }

  /**
   * Instrument a function execution with tracing and metrics
   */
  async instrumentExecution<T>(
    functionId: string,
    functionName: string,
    network: string,
    product: string,
    fn: () => Promise<T>
  ): Promise<{ result?: T; error?: Error; duration: number }> {
    const start = performance.now();
    let result: T | undefined;
    let error: Error | undefined;

    // If OpenTelemetry is not available, just execute the function normally
    if (!this.hasOpenTelemetry) {
      try {
        result = await fn();
      } catch (err) {
        error = err as Error;
      }
      return { result, error, duration: performance.now() - start };
    }

    // Create trace span if tracer is available
    const span = this.tracer?.startSpan(`execute_${functionName}`, {
      attributes: {
        'lit.function.id': functionId,
        'lit.function.name': functionName,
        'lit.network': network,
        'lit.product': product,
      },
    });

    try {
      result = await fn();

      // Mark span as successful
      if (span) {
        try {
          const { SpanStatusCode } = await import('@opentelemetry/api');
          span.setStatus({ code: SpanStatusCode.OK });
        } catch {
          // If we can't import SpanStatusCode, just skip setting status
        }
      }
      
      // Record success metrics
      this.functionExecutionCounter?.add(1, {
        function_name: functionName,
        network,
        product,
        status: 'success',
      });

    } catch (err) {
      error = err as Error;

      // Mark span as error
      if (span) {
        try {
          const { SpanStatusCode } = await import('@opentelemetry/api');
          span.setStatus({
            code: SpanStatusCode.ERROR,
            message: error.message,
          });
          span.recordException(error);
        } catch {
          // If we can't import SpanStatusCode, just skip setting status
        }
      }

      // Record error metrics
      this.functionExecutionCounter?.add(1, {
        function_name: functionName,
        network,
        product,
        status: 'error',
      });

      this.functionErrorCounter?.add(1, {
        function_name: functionName,
        network,
        product,
        error_type: error.constructor.name,
      });

    } finally {
      const duration = performance.now() - start;
      
      // Record duration metrics
      this.functionExecutionDuration?.record(duration, {
        function_name: functionName,
        network,
        product,
        status: error ? 'error' : 'success',
      });

      // Set span attributes and end it
      if (span) {
        span.setAttribute('lit.duration_ms', duration);
        span.end();
      }

      // Emit structured log
      this.logger?.emit({
        severityText: error ? 'ERROR' : 'INFO',
        body: `Function ${functionName} ${error ? 'failed' : 'completed'} in ${duration.toFixed(2)}ms`,
        attributes: {
          'lit.function.id': functionId,
          'lit.function.name': functionName,
          'lit.network': network,
          'lit.product': product,
          'lit.duration_ms': duration,
          'lit.success': !error,
          ...(error && { 'lit.error_message': error.message }),
        },
      });

      return { result, error, duration };
    }
  }

  /**
   * Check if telemetry is enabled and initialized
   */
  isEnabled(): boolean {
    return this.config.enabled && this.isInitialized && this.hasOpenTelemetry;
  }

  /**
   * Shutdown telemetry providers gracefully
   */
  async shutdown(): Promise<void> {
    if (this.hasOpenTelemetry) {
      console.log(' Shutting down Lit Status SDK OpenTelemetry');
    }
    this.isInitialized = false;
    this.hasOpenTelemetry = false;
  }
} 